import styled, { css } from "styled-components";

const ErrorMessageContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  width: 1200px;
  height: 100vh;

  margin: 0 auto;
  padding: 10px;
`;

const ErrorMessageTitle = styled.h1`
  display: flex;

  font-size: 36px;
  line-height: 40px;
  font-weight: 700;

  ${(props) =>
    props.isDarkModeOn
      ? css`
          color: #8c97ac;
        `
      : css`
          color: #000000;
        `}
`;

const ErrorMessageIcon = styled.span`
  display: flex;

  width: 150px;
  height: 150px;

  margin: 25px 0;

  background-image: url("data:image/svg+xml;base64,");
  background-size: contain;

  ${(props) =>
    props.isDarkModeOn
      ? css`
          background-color: #8c97ac;
          border-radius: 50%;
        `
      : css`
          background-color: none;
          border-radius: none;
        `}
`;

const ErrorMessageText = styled.p`
  display: flex;

  font-size: 26px;
  line-height: 30px;
  font-weight: 700;

  ${(props) =>
    props.isDarkModeOn
      ? css`
          color: #8c97ac;
        `
      : css`
          color: #000000;
        `}

  transition: 3s ease;
`;

export {
  ErrorMessageContainer,
  ErrorMessageTitle,
  ErrorMessageIcon,
  ErrorMessageText,
};
